@import '../../Styles/vars.scss';

.header {
	padding: 7.5rem 0 0;
	background: transparent;

	@media screen and (max-width: $large_tablet) {
		padding: 4rem 2rem 0;
	}

	&__container {
		position: relative;
	}

	&__logo-image {
		max-width: 7.6rem;
		max-height: 7.6rem;
		mix-blend-mode: lighten;
		position: absolute;
		left: 15rem;
		top: 50%;
		transform: translateY(-50%);
		object-fit: contain;
		object-position: top;

		@media screen and (max-width: $large_tablet) {
			left: -2rem;
		}
	}

	.category-wrapper {
		display: flex;
		height: 100%;
		align-items: center;
		justify-content: center;

		@media screen and (max-width: $large_tablet) {
			display: none;
		}

		.category {
			font-size: 1.6rem;
			color: #fff;
			padding: 0 2.4rem;
			margin-bottom: 0;
			text-transform: uppercase;
			cursor: pointer;
		}
	}

	.hamburger {
		display: none;
		cursor: pointer;
		max-width: 4.5rem;
		width: 100%;
		max-height: 4.5rem;
		height: 100%;
		object-fit: contain;
		object-position: top;

		@media screen and (max-width: $large_tablet) {
			display: block;
			margin-left: auto;
		}
	}
}
